@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  width:500px;
  margin:auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.5;
  padding-bottom: 200px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appointment-list {
    position: absolute;
    overflow: auto;
    width: 300px;
    height: 476px;
    left: 480px;
    top: 356px;;
    background-color: white;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #efefef;
    box-shadow:
    /* The top layer shadow */
        0 1px 1px rgba(0,0,0,0.15),
            /* The second layer */
        0 10px 0 -5px #eee,
            /* The second layer shadow */
        0 10px 1px -4px rgba(0,0,0,0.15),
            /* The third layer */
        0 20px 0 -10px #eee,
            /* The third layer shadow */
        0 20px 1px -9px rgba(0,0,0,0.15);
    padding: 30px;
}

.appointment-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 20px 0;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

.calendar-wrapper{
  position: relative;
  z-index: 3;
  padding: 100px;
  font-size:16px;
  color: #999999;
}

.calendar-wrapper  * {
  box-sizing: border-box;
}
.calendar-block{
  position: relative;
  display: block;
  width: 100%;
  max-width: 300px;
  margin: auto;
  padding: 30px;
  background-color: white;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.calendar-wrapper form input {
  display: inline-block;
  width: 100%;
  padding: 0 20px;
  line-height: 50px;
  height: 50px;
  background-color: white;
  border: 1px solid #e6e6e6;
  border-bottom-width: 3px;
  border-radius: 5px;
  outline: 0;
  transition: all 0.3s ease;
}

.calendar-wrapper form input:not(:last-child) {
  margin-bottom: 20px;
}

.calendar-wrapper form input[type="input_appt_title"]:focus, .final__register-wrapper form input[type="input_appt_title"]:active, .final__register-wrapper form input[type="input_appt_time"]:focus, .final__register-wrapper form input[type="input_appt_time"]:active {
  border-color: #cccccc;
}

.calendar-wrapper table{
 margin-left: 30px
}
.calendar-wrapper button {
  background: #f6f7f9;
  border: 1px solid #ced0d4;
  border-radius: 2px;
  color: #4b4f56;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 6px 12px;
}

.calendar-wrapper button:hover {
  background: #e9ebee;
}
.calendar-wrapper button:active {
  background: #d8dade;
  border-color: #d8dade;
}
.calendar-title{
     font-size: 1.5em;
     padding-bottom: 20px;
     margin: 0 0 20px;
     border-bottom: 1px dashed #e6e6e6;
}

